<template>
    <component :is="currentComponent" />
</template>

<script>
import LoginPage from './pages/LoginPage.vue';
import HomePage from './pages/HomePage.vue';
import { mapGetters } from 'vuex';

export default {
  name: 'App',

  components:{
    LoginPage,
    HomePage
  },

  computed:{
    ...mapGetters(['getLogged']),

    currentComponent(){
      if(this.getLogged){
        return 'HomePage'
      }else{
        return 'LoginPage'
      }
    }
    
  }
  

}
</script>

